import React, { useState, useEffect } from "react";
import { styled } from "@material-ui/core/styles";
import {
  Avatar,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TablePagination,
  TableRow,
  Card,
  CardHeader,
  Stack,
  Typography,
  Container,
} from "@material-ui/core";
import { useSelector } from "react-redux";

import {
  RootAppState,
  WaitingListAdminObject,
} from "../../../../../utils/typeHelpers";
import { applySortFilterhistory } from "./Goals";
import { customShadows } from "../../../../../theme/shadows";
import { ReviewModal, UserListHead, UserListToolbar } from "./components";
import Scrollbar from "../../../../../components/Scrollbar";

import SearchNotFound from "../../../../../components/SearchNotFound";
import Loader from "../../../../../components/Loader";
import useAxios from "../../../../../utils/useAxios";
import { UseMutationResult, useMutation } from "react-query";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";

function getComparator(order: string, orderBy: keyof WaitingListAdminObject) {
  return order === "desc"
    ? (a: WaitingListAdminObject, b: WaitingListAdminObject) =>
        descendingComparator(a, b, orderBy)
    : (a: WaitingListAdminObject, b: WaitingListAdminObject) =>
        -descendingComparator(a, b, orderBy);
}

function descendingComparator(
  a: WaitingListAdminObject,
  b: WaitingListAdminObject,
  orderBy: keyof WaitingListAdminObject
) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

const TABLE_HEAD = [
  { id: "image", label: "Goal Image", alignRight: false },
  { id: "title", label: "Goal Title", alignRight: false },
  { id: "username", label: "Username", alignRight: false },
  { id: "profile_type", label: "Profile Type", alignRight: false },
  { id: "avatar", label: "Avatar", alignRight: false },
  { id: "requested_amount", label: "Requested Amount", alignRight: false },
  { id: "remark", label: "Remark", alignRight: false },
  { id: "status", label: "Status", alignRight: false },
  { id: "action", label: "Action", alignRight: false },
];

const WaitingNgoList = () => {
  const axiosIntance = useAxios();
  const profileId = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.user_profile?.id
  );

  const [refecth, setRefetch] = useState(false);

  const WaitingNgoList: UseMutationResult<
    WaitingListAdminObject[],
    Error,
    number
  > = useMutation(["fundTransferHistoryMutation"], async (dataReq) => {
    var formdata = new FormData();
    formdata.append("profile", `${dataReq}`);

    const response = await axiosIntance.post(
      `/waiting-for-ngo-list/`,
      formdata
    );
    return response.data;
  });

  useEffect(() => {
    if (profileId !== undefined) {
      WaitingNgoList.mutate(profileId);
    }
  }, []);

  const { data, isLoading, isError, error } = WaitingNgoList;

  const handleRefetch = () => setRefetch(!refecth);
  console.log(data);

  return (
    <Container maxWidth="xl" sx={{ minHeight: "100vh" }}>
      {isLoading ? (
        <Loader />
      ) : isError ? (
        <ApiErrorPage error={error ? error : new Error("Network Error")} />
      ) : (
        data && <AdminWaiting data={data} handleRefetch={handleRefetch} />
      )}
    </Container>
  );
};

export default WaitingNgoList;

const AdminWaiting = ({
  data,
  handleRefetch,
}: {
  data: WaitingListAdminObject[];
  handleRefetch: () => void;
}) => {
  const profile_image = useSelector(
    (state: RootAppState) =>
      state.AuthReducer.userInfo.user_profile?.profile_image
  );

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [filterName, setFilterName] = useState("");
  const [order, setOrder] = useState("asc");
  const [open, setOpen] = useState(false);
  const [orderBy, setOrderBy] = useState<keyof WaitingListAdminObject>("id");

  const [id, setId] = useState<null | number>(null);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleFilterByName = (event: React.ChangeEvent<HTMLInputElement>) => {
    setFilterName(event.target.value);
  };

  const handleRequestSort = (
    event: React.ChangeEvent<HTMLInputElement>,
    property: keyof WaitingListAdminObject
  ) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const filteredUsers = applySortFilterhistory(
    data,
    getComparator(order, orderBy),
    filterName
  );

  const isUserNotFound = data.length === 0;

  const handleChangePage = (event: any, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const OPTIONS = [
    { value: "ACCEPTED", label: "ACCEPTED" },
    { value: "REJECTED_BY_NGO", label: "REJECTED BY NGO" },
  ];

  return (
    <Card sx={{ boxShadow: customShadows.z1 }}>
      <ReviewModal
        open={open}
        handleClose={handleClose}
        id={id}
        handleRefetch={handleRefetch}
        OPTIONS={OPTIONS}
      />
      <CardHeader title="Ngo Waiting List Table" />

      <UserListToolbar
        filterName={filterName}
        onFilterName={handleFilterByName}
      />

      <Scrollbar sx={{}}>
        <TableContainer sx={{ minWidth: 800 }}>
          <Table>
            <UserListHead
              order={order}
              orderBy={orderBy}
              headLabel={TABLE_HEAD}
              rowCount={data.length}
              onRequestSort={handleRequestSort}
            />
            <TableBody>
              {filteredUsers
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row: WaitingListAdminObject) => {
                  const {
                    id,
                    goal,
                    profile,
                    requested_amount,
                    remark,
                    status,
                  } = row;

                  return (
                    <TableRow>
                      <TableCell align="left">
                        {goal === null ? (
                          <Avatar src={profile_image} />
                        ) : (
                          <Avatar src={goal.image} />
                        )}
                      </TableCell>
                      {goal === null ? (
                        <GoalName align="left">Donee Amount</GoalName>
                      ) : (
                        <GoalName align="left">{goal.title}</GoalName>
                      )}
                      <GoalName align="left">{profile.username}</GoalName>
                      <GoalName align="left">{profile.profile_type}</GoalName>
                      <TableCell align="left">
                        <Avatar src={profile.image} />
                      </TableCell>
                      <TableCell align="left">${requested_amount}</TableCell>
                      <TableCell align="left">{remark}</TableCell>
                      <TableCell align="left">
                        <Stack
                          direction="row"
                          alignItems="center"
                          spacing={2}
                          justifyContent="space-between"
                        >
                          <Typography>{status}</Typography>
                        </Stack>
                      </TableCell>
                      <TableCell align="left">
                        <Button
                          color="info"
                          onClick={async () => {
                            await setId(id);
                            await handleOpen();
                          }}
                        >
                          Change Status
                        </Button>
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
            {isUserNotFound && (
              <TableBody>
                <TableRow>
                  <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                    <SearchNotFound searchQuery={filterName} />
                  </TableCell>
                </TableRow>
              </TableBody>
            )}
          </Table>
        </TableContainer>
      </Scrollbar>

      <TablePagination
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={data.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Card>
  );
};

const GoalName = styled(TableCell)(() => ({
  color: "#343A40",
  fontsize: 14,
  fontWeight: "normal",
  textTransform: "capitalize",
}));
